export default{ 
	props:{
		controls:{
			type:Boolean,
			default:false
		},
		src:{
			type:String,
			default:'http://baobab.kaiyanapp.com/api/v1/playUrl?vid=129764&resourceType=video&editionType=default&source=aliyun&playUrlType=url_oss'
		},
		play:{
			type:Boolean,
			default:false
		},
		height:{
			type:String,
			default:''
		},
		width:{
			type:String,
			default:''
		},
		initialTime:{
			type:Number,
			default:0
		},
		gDuration:{
			type:Number,
			default:999
		}, //大概时长使进度条更准确
		objectFit:{
			type:String,
			default:'contain'
		}
	},
	data(){
		return{
			time:0,
			duration:0,
			playFirst:true
		}
	},
	mounted() {
		this.videoCtx = uni.createVideoContext(`video_${this.src}`,this)
		
	},
	methods:{
		timeupdate(event){
			this.duration = event.detail.duration
			if(!this.play) return
			if(this.time>=this.duration) this.time=0
			this.time = event.detail.currentTime
		},
		videoPlay(){
			if(this.play){
				this.videoCtx.play();
				if(this.playFirst){
					this.videoCtx.seek(this.startTime)
					this.playFirst = false
				} 
			}else{
				this.videoCtx.pause();
				this.$emit('pause',this.time)
			}
			
		},
		waiting(){
			
		}
	},
	watch:{
		play(newVal,oldVal){
			this.videoPlay()
		},
		startTime:{
			immediate: true,
			handler(newVal,oldVal){
				
				this.time = newVal
			}
		},
		gDuration:{
			immediate: true,
			handler(newVal,oldVal){
				
				this.duration = newVal
			}
		}
	},
	computed:{
		barWidth(){
			
			let width = this.time/this.duration*parseInt(this.width)
			
			return `${width}px`
		},
		startTime(){
			return this.initialTime
		}
	}
}
